import React from 'react'
import { connect } from 'react-redux'
import moment from 'moment';
import { RadioButtonGroup } from '@tencent/bere'
import { DateRangePicker } from '@components/DateTime'

class Schedule extends React.Component {
  handleScheduleChange = timeType => {
    let { timeStart, timeEnd } = this.props
    if(timeType==-1){
      let now = new Date();
      let strDate = moment(now).format('YYYY-MM-DD');

      timeStart = timeStart || strDate
      timeEnd = timeEnd || strDate
    }
    this.props.dispatch({
      type: 'statis/change',
      payload: { timeType,timeStart, timeEnd}
    })
  }

  handleScheduleTimeChange = ({ from, to }) => {
    this.props.dispatch({
      type: 'statis/change',
      payload: {
        timeStart: from.format('YYYY-MM-DD'),
        timeEnd: to.format('YYYY-MM-DD')
      }
    })
  }

  render() {
    const { timeType } = this.props
    let { timeStart, timeEnd } = this.props
    if (!timeStart) {
      timeStart = timeEnd = new Date()
    }
    return (
      <li>
        <div className="form-input">
          <div className="form-unit">
            <RadioButtonGroup
              value={timeType}
              list={[
                { text: '今天', value: 1 }
                ,{ text: '昨天', value: 2 }
                ,{ text: '近7天', value: 3 }
                ,{ text: '近30天', value: 4 }
                ,{ text: '自选时间', value: -1 }
              ]}
              onChange={this.handleScheduleChange}
            />
            {timeType === -1 ? (
              <DateRangePicker
                value={{ from: timeStart, to: timeEnd }}
                isUntilNow={false}
                onChange={this.handleScheduleTimeChange}
              />
            ) : (
              ''
            )}
          </div>
        </div>
      </li>
    )
  }
}

export default connect(({ statis }) => {
  return {
    timeType: statis.timeType,
    timeStart: statis.timeStart,
    timeEnd: statis.timeEnd
  }
})(Schedule)
