import React from 'react'
import { connect } from 'react-redux'
import { Pager } from '@tencent/bere'

class StatisPager extends React.Component {
  handlePageChange = page => {
    this.props.dispatch({
      type: 'statis/change',
      payload: { page }
    })

    this.props.dispatch({ type: 'statis/refresh' })
  }

  handlePerPageChange = limit => {
    this.props.dispatch({
      type: 'statis/change',
      payload: { limit, page: 0 }
    })

    this.props.dispatch({ type: 'statis/refresh' })
  }

  render() {
    const { total, page, limit } = this.props
    return (
      <Pager
        total={total}
        current={page}
        perPageCountList={[15, 30, 45]}
        perPageCount={limit}
        onPageChange={this.handlePageChange}
        onPerPageChange={this.handlePerPageChange}
      />
    )
  }
}

export default connect(({ statis }) => {
  return {
    page: statis.page,
    limit: statis.limit,
    total: statis.total
  }
})(StatisPager)
