import React from 'react'
import { connect } from 'react-redux'
import { DataTable } from '@tencent/bere'
import './style.css'



class StatisTable extends React.Component {
  constructor(props){
    super(props);
    this.state = {filterData:{}};
  }
  componentDidMount() {
    this.props.dispatch({
      type: 'statis/change',
      payload: { filters:{}}
    })
  }
  get columns() {
    const { filters } = this.props
    let filterData = this.state.filterData;
    let columns_data = {
      "Sum":[//数据概览
        { key: 'rtir_start_time',dataIndex: 'rtir_start_time',title: 'RTIR开始时间' },
        { key: 'rtir_end_time',dataIndex: 'rtir_end_time',title: 'RTIR结束时间' },
        { key: 'rtir_glass_num',dataIndex: 'rtir_glass_num',title: 'RTIR-GLASS总量' },
        { key: 'adc_glass_num',dataIndex: 'adc_glass_num',title: 'ADC-GLASS总量' },
        { key: 'rtir_img_num',dataIndex: 'rtir_img_num',title: 'RTIR-图片总量' },
        { key: 'adc_img_num',dataIndex: 'adc_img_num',title: 'ADC-图片总量' },
        { key: 'uniformity_img',dataIndex: 'uniformity_img',title: '判定结果一致数量(IMAGE)' },
        { key: 'adc_diff_img',dataIndex: 'adc_diff_img',title: '判定结果差异数量(IMAGE)' },
        { key: 'adc_accuracy_rate',dataIndex: 'adc_accuracy_rate',title: 'ADC平均准确率' },
        { key: 'confidence_level',dataIndex: 'confidence_level',title: '置信度' },
        { key: 'adc_cov_rate',dataIndex: 'adc_cov_rate',title: '覆盖率' },
        { key: 'uniformity_turn_on_rate',dataIndex: 'uniformity_turn_on_rate',title: 'TURN ON一致率' },
        { key: 'turn_on_leak_rate',dataIndex: 'turn_on_leak_rate',title: '漏单' }
      ],  
      "Code":[//准确率与覆盖率
        { key: 'handling_time',dataIndex: 'handling_time',title: '日期'},
        { key: 'code',dataIndex: 'code',title: 'CODENAME',
        headerRender: () => (
          <DataTable.FilterableColumnHeader
            list={filterData.codes && filterData.codes.map(value => ({ text: value, value }))}
            value={filters.code}
            onFilter={this.handleFileter("code")}
          >
            CODENAME
          </DataTable.FilterableColumnHeader>
        )},
        { key: 'total',dataIndex: 'total',headerRender:(value, record)=>{
          return (<div>
            <span>总计 </span>
            <div className="tc-15-bubble-icon tc-15-triangle-align-start">
              <i className="tc-icon icon-what" />
              <div className="tc-15-bubble tc-15-bubble-top">
                <div className="tc-15-bubble-inner">
                  <p>RTIR的识别结果中对应CODE的总量</p>
                </div>
              </div>
            </div>
          </div>)
        }},
        
        { key: 'ok',dataIndex: 'ok',title: 'OK',headerRender:(value, record)=>{
          return (<div>
              <span>OK </span>
              <div className="tc-15-bubble-icon tc-15-triangle-align-start">
                <i className="tc-icon icon-what" />
                <div className="tc-15-bubble tc-15-bubble-top">
                  <div className="tc-15-bubble-inner">
                    <p>ADC识别为对应CODE中，正确的数量</p>
                  </div>
                </div>
              </div>
            </div>)
          }
        },
        { key: 'ng',dataIndex: 'ng',title: 'NG',headerRender:(value, record)=>{
          return (<div>
              <span>NG </span>
              <div className="tc-15-bubble-icon tc-15-triangle-align-start">
                <i className="tc-icon icon-what" />
                <div className="tc-15-bubble tc-15-bubble-top">
                  <div className="tc-15-bubble-inner">
                    <p>ADC识别为对应CODE中，错误的数量</p>
                  </div>
                </div>
              </div>
            </div>)
          }
        },
        { key: 'accuracy_rate',dataIndex: 'accuracy_rate',title: '准确率',headerRender:(value, record)=>{
          return (<div>
              <span>准确率 </span>
              <div className="tc-15-bubble-icon tc-15-triangle-align-start">
                <i className="tc-icon icon-what" />
                <div className="tc-15-bubble tc-15-bubble-top">
                  <div className="tc-15-bubble-inner">
                    <p>OK/(OK+NG)</p>
                  </div>
                </div>
              </div>
            </div>)
          },
        },
        { key: 'cov_rate',dataIndex: 'cov_rate',title: '覆盖率',headerRender:(value, record)=>{
          return (<div>
              <span>覆盖率 </span>
              <div className="tc-15-bubble-icon tc-15-triangle-align-start">
                <i className="tc-icon icon-what" />
                <div className="tc-15-bubble tc-15-bubble-top">
                  <div className="tc-15-bubble-inner">
                    <p>OK/总计，当值大于1时，取1</p>
                  </div>
                </div>
              </div>
            </div>)
          },
        }
      ],  
      "Flagdiff":[//Turn on Flag 比对
        { key: 'handling_time',dataIndex: 'handling_time',title: '日期'},
        { key: 'process_operation_name',dataIndex: 'process_operation_name',title: '站点'},
        { key: 'product_spec_name',dataIndex: 'product_spec_name',title: '产品'},
        { key: 'glass_id',dataIndex: 'glass_id',title: 'GlassID'},
        { key: 'img_id',dataIndex: 'img_id',title: 'ImageName',
          render: (value, record) =>{
            if(record.img_url){
              return (<div>
                <a href={record.img_url} target="_blank">{value}</a>
                </div>)
            }
            else{
              return (<div>{value}</div>)
            }
          }
        },
        { key: 'related_disconnect',dataIndex: 'related_disconnect',title: '相关断线结果'},
        { key: 'rtir_turn_on',dataIndex: 'rtir_turn_on',title: 'RTIR Turn On结果'},
        { key: 'turn_on',dataIndex: 'turn_on',title: 'ADC Turn On结果'},
        { key: 'confidence_level',dataIndex: 'confidence_level',title: '置信度'},
        { key: 'rtir_code',dataIndex: 'rtir_code',title: 'RTIR-CODE'},
        { key: 'code',dataIndex: 'code',title: 'ADC-CODE'}
      ],
      "Flagleak":[//Turn on Flag漏单
        { key: 'handling_time',dataIndex: 'handling_time',title: '日期'},
        { key: 'process_operation_name',dataIndex: 'process_operation_name',title: '站点'},
        { key: 'product_spec_name',dataIndex: 'product_spec_name',title: '产品'},
        { key: 'glass_id',dataIndex: 'glass_id',title: 'GlassID'},
        { key: 'img_id',dataIndex: 'img_id',title: 'ImageName',
          render: (value, record) =>{
            if(record.img_url){
              return (<div>
                <a href={record.img_url} target="_blank">{value}</a>
                </div>)
            }
            else{
              return (<div>{value}</div>)
            }
          }
        },
        { key: 'rtir_turn_on',dataIndex: 'rtir_turn_on',title: 'RTIR Turn On'},
        { key: 'turn_on',dataIndex: 'turn_on',title: 'ADC Turn On'},
        { key: 'rtir_rework',dataIndex: 'rtir_rework',title: 'RTIR-REWORK'},
        { key: 'rework',dataIndex: 'rework',title: 'ADC-REWORK'},
        { key: 'related_disconnect',dataIndex: 'related_disconnect',title: 'related_disconnect'},
        { key: 'confidence_level',dataIndex: 'confidence_level',title: '置信度'},
        { key: 'rtir_code',dataIndex: 'rtir_code',title: 'RTIR-CODE'},
        { key: 'code',dataIndex: 'code',title: 'ADC-CODE'}
      ],
      "Diffdetail":[//结果差异明细
        { key: 'handling_time',dataIndex: 'handling_time',title: '日期'},
        { key: 'process_operation_name',dataIndex: 'process_operation_name',title: '站点'},
        { key: 'product_spec_name',dataIndex: 'product_spec_name',title: '产品别'},
        { key: 'glass_id',dataIndex: 'glass_id',title: 'GLASSID'},
        { key: 'img_id',dataIndex: 'img_id',title: '图片ID',
          render: (value, record) =>{
            if(record.img_url){
              return (<div>
                <a href={record.img_url} target="_blank">{value}</a>
                </div>)
            }
            else{
              return (<div>{value}</div>)
            }
          }
        },
        { key: 'code',dataIndex: 'code',title: 'ADC-CODE'},
        { key: 'turn_on',dataIndex: 'turn_on',title: 'ADC-TURN ON'},
        { key: 'rework',dataIndex: 'rework',title: 'ADC-REWORK'},
        { key: 'rtir_code',dataIndex: 'rtir_code',title: 'RTIR-CODE'},
        { key: 'rtir_turn_on',dataIndex: 'rtir_turn_on',title: 'RTIR-TURN ON'},
        { key: 'rtir_rework',dataIndex: 'rtir_rework',title: 'RTIR-REWORK'}
      ],
      "Leakdetail":[//RTIR缺失明细
        { key: 'handling_time',dataIndex: 'handling_time',title: '日期'},
        { key: 'process_operation_name',dataIndex: 'process_operation_name',title: '站点'},
        { key: 'product_spec_name',dataIndex: 'product_spec_name',title: '产品别'},
        { key: 'glass_id',dataIndex: 'glass_id',title: 'GLASSID'},
        { key: 'img_id',dataIndex: 'img_id',title: '图片ID',
          render: (value, record) =>{
            if(record.img_url){
              return (<div>
                <a href={record.img_url} target="_blank">{value}</a>
                </div>)
            }
            else{
              return (<div>{value}</div>)
            }
          }
        },
        { key: 'code',dataIndex: 'code',title: 'ADC-CODE'},
        { key: 'turn_on',dataIndex: 'turn_on',title: 'ADC-TURN ON'}
      ]
    }
    const { serviceName } = this.props
    return columns_data[serviceName];
  }
  handleFileter(field) {
    return (value)=>{
      let { filters } = this.props
      filters[field] = value;
      this.props.dispatch({
        type: 'statis/change',
        payload: { filters}
      })
      this.props.dispatch({ type: 'statis/refresh' })
    }
  }
  render() {
    let { dataList,serviceName } = this.props
    const { isLoading} = this.props
    if(serviceName == 'Code' && dataList && dataList.length>0 && (!this.state.filterData.codes || this.state.filterData.codes.length==0)){
      let codes = []
      dataList.forEach(function(el) {
        if(!codes.includes(el.code)){
          codes.push(el.code);
        }
      });
      this.state.filterData.codes = codes;
    }
    return (
      <DataTable
        columns={this.columns}
        loading={isLoading}
        data={dataList}
        rowKey="id"
        sortKey="handling_time"
      />
    )
  }
}

export default connect(function ({ statis }) {
  return {
    isLoading: statis.isLoading,
    dataList: statis.dataList,
    serviceName :statis.serviceName,
    filters :statis.filters
  }
})(StatisTable)
