import React from 'react'
import { connect } from 'react-redux'
import { FormGroupList,Button,Input,TopTips } from '@tencent/bere'

import ScheduleControl from './ScheduleControl'
import SiteControl from './SiteControl'

import StatisTable from './StatisTable'
import StatisPager from './StatisPager'

class App extends React.Component {
  componentDidMount = () => {
    let serviceName= this.props.match.params["category"];
    this.props.dispatch({
      type: 'statis/change',
      payload: { serviceName,page:0}
    })
    const { site, shop, operation,product,timeType,timeStart,timeEnd,page, limit,filters,confidence_level } = this.props
      this.props.dispatch({ type: 'statis/fetchDatas',
      payload: { site, shop, operation,product,timeType,timeStart,timeEnd,page,limit,serviceName,confidence_level} 
    })
  }

  handleSearch = () => {
  const { site, shop, operation,product,timeType,timeStart,timeEnd,page, limit,filters,confidence_level } = this.props
    if(product && product!="-1"){
      this.props.dispatch({ type: 'statis/fetchDatas',
        payload: { site, shop, operation,product,timeType,timeStart,timeEnd,page,limit,filters,confidence_level,serviceName:this.props.match.params["category"]} 
      })
    }
    else{
      return TopTips({ theme: 'error', message: '仅支持查询单个产品，请先选择产品' })
    }
  }


  render() {
    let categoryNameMap = {'Sum':'数据概览（置信度98）','Code':'准确率与覆盖率','Flagdiff':'Turn on Flag 比对','Flagleak':'Turn on Flag漏单','Diffdetail':'结果差异明细','Leakdetail':'RTIR缺失明细'}
    

    const { serviceName } = this.props
    
    let categoryName = categoryNameMap[serviceName]
    
    return (
      <div className="main adc-manage-main">


      <div className="manage-area">
        <div className="manage-area-title">
          <h2>{categoryName}</h2>
        </div>
        <div className="manage-area-main">
          <div className="manage-area-main-inner">

          <div className="tc-action-grid">
            <div className="justify-grid">
              <FormGroupList>
                <ScheduleControl />
                <SiteControl />
              </FormGroupList>
            </div>
            <div className="justify-grid">
                <br />
                <Button className="m btn-add" onClick={this.handleSearch}>
                  确定
                </Button>
            </div>
          </div>
          <div className="tc-panel panel-table statis-style">
            <StatisTable />
            <StatisPager />
          </div>
        </div>
      </div>
      </div>
      </div>
    )
  }
}

export default connect(({ statis }) => {
  return {
    isLoading: statis.isLoading,
    site: statis.site,
    shop: statis.shop,
    operation: statis.operation,
    product: statis.product,
    timeType: statis.timeType,
    timeStart: statis.timeStart,
    timeEnd: statis.timeEnd,
    page: statis.page,
    limit: statis.limit,
    serviceName:statis.serviceName,
    filters:statis.filters,
    confidence_level:statis.confidence_level
    }
})(App)
