import React from 'react'
import { connect } from 'react-redux'
import { Select,Input } from '@tencent/bere'

const ascSort = (a, b) => (a > b ? 1 : -1)
const descSort = (a, b) => (b > a ? 1 : -1)

const addSelAll = (arr) => ((arr && arr.length>0)?arr.splice(0,0,{text:'全部',value:'-1'}):'')

class Site extends React.Component {
  componentDidMount() {
    this.props.dispatch({ type: 'siteMap/fetchSiteMap' })
  }

  handleSiteChange = site => {
    this.props.dispatch({
      type: 'siteMap/change',
      payload: {productList:[] }
    })
    this.props.dispatch({
      type: 'statis/change',
      payload: { site, shop: '', operation: '',product:'' }
    })
  }

  handleShopChange = shop => {
    this.props.dispatch({
      type: 'siteMap/change',
      payload: { productList:[] }
    })
    this.props.dispatch({
      type: 'statis/change',
      payload: { shop, operation: '',product:'' }
    })
  }

  handleOperationChange = operation => {
    const { site, shop } = this.props
    this.props.dispatch({
      type: 'siteMap/fetchProducts',
      payload: { site, shop, operation }
    })
    this.props.dispatch({
      type: 'statis/change',
      payload: { operation,product:'' }
    })
  }
  handleProductChange = product => {
    this.props.dispatch({
      type: 'statis/change',
      payload: { product }
    })
  }
  handleConfidence_levelChange = e => {
    let confidence_level = e.target.value
    console.log(confidence_level)
    let cl = parseFloat(confidence_level);
    console.log(cl)
    if(Number.isNaN(cl) || cl<0 || cl>100){return;}
    this.props.dispatch({
      type: 'statis/change',
      payload: { confidence_level:cl }
    })
  }
  
  handleAlgorithm_versionChange = e => {
    this.props.dispatch({
      type: 'statis/change',
      payload: { algorithm_version:e.target.value }
    })
  }

  
  handleRule_versionChange = e => {
    this.props.dispatch({
      type: 'statis/change',
      payload: { rule_version:e.target.value }
    })
  }

  render() {
    const { siteMap, site, shop, operation,product,productList,serviceName,confidence_level,rule_version,algorithm_version } = this.props

    let curSite
    let shopList
    let operationList
    const siteList = siteMap
      .map(item => {
        if (item.site === site) {
          curSite = item
        }
        return { text: item.site, value: item.site }
      })
      .sort((a, b) => ascSort(a.text, b.text))
    if (curSite && curSite.factory_list) {
      let curShop
      shopList = curSite.factory_list
        .map(item => {
          if (item.factory_name === shop) {
            curShop = item
          }
          return { text: item.factory_name, value: item.factory_name }
        })
        .sort((a, b) => ascSort(a.text, b.text))

      if (curShop && curShop.pon_list) {
        operationList = curShop.pon_list
          .map(item => ({
            text: item.process_operation_name,
            value: item.process_operation_name
          }))
          .sort((a, b) => ascSort(a.text, b.text))
      }
    }

    // addSelAll(siteList)
    // addSelAll(shopList)
    // addSelAll(operationList)
    // addSelAll(productList)

    return (
      <div className="tc-15-mod-selector-area">
        <Select
          className="tc-15-select s mr-10"
          value={site}
          list={siteList}
          selectFirstByDefault={false}
          placeholder="请选择厂区"
          onChange={this.handleSiteChange}
        />
        <Select
          className="tc-15-select s mr-10"
          value={shop}
          list={shopList}
          selectFirstByDefault={false}
          placeholder="请选择厂别"
          onChange={this.handleShopChange}
          disabled={!site}
        />
        <Select
          className="tc-15-select s"
          value={operation}
          list={operationList}
          selectFirstByDefault={false}
          placeholder="请选择站点"
          onChange={this.handleOperationChange}
          disabled={!shop}
        />
        <Select
          className="tc-15-select s"
          value={product}
          list={productList}
          selectFirstByDefault={false}
          placeholder="请选择产品"
          onChange={this.handleProductChange}
          disabled={!operation}
        />
        {
          serviceName=='Code'?
          (<span>&nbsp;&nbsp;&nbsp;&nbsp;置信度阈值：<Input  style={{"minwidth":"83px"}} value={confidence_level} className="tc-15-input-text s"
        onChange={this.handleConfidence_levelChange} /></span>):''
        }
        <span>&nbsp;&nbsp;&nbsp;&nbsp;算法版本：<Input  style={{"minwidth":"83px"}} value={rule_version} className="tc-15-input-text s"
        onChange={this.handleRule_versionChange} /></span>
        <span>&nbsp;&nbsp;&nbsp;&nbsp;规则版本：<Input  style={{"minwidth":"83px"}} value={algorithm_version} className="tc-15-input-text s"
        onChange={this.handleAlgorithm_versionChange} /></span>
      </div>
    )
  }
}

export default connect(({ siteMap,statis }) => {
  return {
    serviceName :statis.serviceName,
    siteMap: siteMap.siteMap,
    productList: siteMap.productList,
    site: statis.site,
    shop: statis.shop,
    operation: statis.operation,
    product: statis.product,
    confidence_level:statis.confidence_level,
    rule_version:statis.rule_version,
    algorithm_version:statis.algorithm_version
  }
})(Site)
